.form {
  position: relative;
  z-index: 1;
  background: #48c9b0;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: FontAwesome, "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  border-radius: 10px;
}
.form button {
  font-family: "Titillium Web", sans-serif;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 0.1em;
  outline: 0;
  background: #17a589;
  width: 100%;
  border: 0;
  border-radius: 30px;
  margin: 0px 0px 8px;
  padding: 15px;
  color: #ffffff;
  transition: all 0.3 ease;
  cursor: pointer;
  transition: all 0.2s;
}
.form button:hover,
.form button:focus {
  background: #148f77;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  transform: translateY(-4px);
}
.form button:active {
  transform: translateY(2px);
  box-shadow: 0 2.5px 5px rgba(0, 0, 0, 0.2);
}

.form .message {
  margin: 6px 6px;
  color: #808080;
  font-size: 11px;
  text-align: center;
  font-weight: bold;
  font-style: normal;
}
.form .message a {
  color: #ffffff;
  text-decoration: none;
  font-size: 13px;
}
.form .register-form {
  display: none;
}
.container_login {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container_login:before,
.container_login:after {
  content: "";
  display: block;
  clear: both;
}
.container_login .info {
  margin: 50px auto;
  text-align: center;
}
.container_login .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container_login .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container_login .info span a {
  color: #000000;
  text-decoration: none;
}
.container_login .info span .fa {
  color: #ef3b3a;
}



